import {liveData} from "../../../api/user.js"
export default {
	data(){
		return {
			id:'',
			info:{}
		}
	},
	onLoad(opts) {
		uni.hideShareMenu() 
		this.id = opts.carId
		this.getLiveData()
	},
	methods:{
		getLiveData(){
			uni.showLoading()
			liveData({
				data:{
					car_id:this.id
				}
			}).then(res=>{
				uni.hideLoading()
				if(res.data.code == 0) {
					this.info = res.data.result.car_obd
				}
			})
		}
		
	}
}
